<template>
  <div id="card_group">
    <b-card-group columns>
      <b-card v-for="(m_card, index) in cards"
                :key="index"
                :title="m_card.title"
                :img-src="m_card.img_src"
                img-fluid
                img-alt="image"
                img-top>
        <p class="card-text">{{ m_card.abstract }}</p>
        <router-link :to="'/article/' + m_card.title.replace(/ /g, '-')" tag="a">阅读全文</router-link>
        <div slot="footer">
          <small class="text-muted">最后更新：{{ m_card.update_time }}</small>
        </div>
      </b-card>
      <b-card title="查看全部文章">
        <router-link to="/article" tag="a">文章列表</router-link>
      </b-card>
    </b-card-group>
  </div>
</template>

<script>
export default {
  created: function(){
    this.init_cards(this);
  },
  name: 'BlogList',
  methods:{
    init_cards: function(_) {
    console.log('init_cards')
      this.$ajax.get('https://chenyuxian0602.github.io/data/json/cards.json')
        .then(function(res) {
          _.cards = res.data;
        })
        .catch(function(err) {
          console.log(err)
        });
    }
  },
  data() {
    return {
      cards: []
    }
  }
}
</script>

<style>
#card_group {
  width: 70%;
  margin: 1% auto 0;
}
</style>



